// Single view
export const useSingleView = ({dispatch,setLocalEntryData,setSummary,setSearch,getStockTakeSummary,search,warehouseUuid,areaId, localEntryData,moduleName}) => {
  //submit
const [action, setAction] = useState()
const [confirmation, setConfirmation] = useState({ show: false, name: 'submit' })
const [isSuccess, setIsSuccess] = useState({ success: false, name: null })
const [isSaved, setIsSaved] = useState()

// create po single view
const [createPo, setCreatePo] = useState()
const [singleViewData, setSingleViewData] = useState()


  //condition move all
  const [moveAll, setMoveAll] = useState()

useEffect(() => {
  if (isSuccess.status === 500) {
      setIsSaved(false)
      setConfirmation({ show: false, name: confirmation.name })
  }
  else if (isSuccess.status === 200) {
      setConfirmation({ show: false, name: confirmation.name })
      setIsSaved(true)
  }

}, [isSuccess])

useEffect(() => {
    if (!isSaved) setIsSuccess({ success: false, name: null })
}, [isSaved])
const postInfo = isSuccess.success && isSuccess.status ? true : false

// end of submit

  //singleview
  const updateList = (props) => {    
    setLocalEntryData()
    setSummary()
    dispatch({type:IS_LOADING, isLoading:true})
    setTimeout(() => {
      getStockTakeSummary({ 
        moduleName,
        zeroQuantity : props,
        singleView:true,
        dispatch,
        desc:search,
        warehouseUuid,
        areaId,
        setModule: setSummary,
        module:localEntryData },
        setSearch(search))
    }, 10);
  }

  return {updateList,action, setAction,confirmation, setConfirmation,isSuccess, setIsSuccess,isSaved, setIsSaved,createPo, setCreatePo,singleViewData, setSingleViewData,postInfo,moveAll, setMoveAll}
}

